import type { Assignment } from "@/lib/types";

type CanvasTodo = {
  assignment?: {
    id?: number;
    name?: string;
    due_at?: string;
    course_id?: number;
    points_possible?: number;
  };
  context_name?: string;
};

function estimateMinutes(pointsPossible?: number) {
  if (!pointsPossible || pointsPossible < 25) return 45;
  if (pointsPossible < 60) return 75;
  return 110;
}

function getPriorityFromDueDate(dueAt: string) {
  const hours = (new Date(dueAt).getTime() - Date.now()) / (1000 * 60 * 60);

  if (hours <= 36) return "high" as const;
  if (hours <= 72) return "medium" as const;
  return "low" as const;
}

export async function fetchCanvasAssignments(baseUrl: string, token: string): Promise<Assignment[]> {
  try {
    const cleanBase = baseUrl.replace(/\/$/, "");
    const url = `${cleanBase}/api/v1/users/self/todo?per_page=20`;

    const res = await fetch(url, {
      headers: {
        Authorization: `Bearer ${token}`,
        Accept: "application/json"
      },
      next: { revalidate: 300 }
    });

    if (!res.ok) {
      return [];
    }

    const todos = (await res.json()) as CanvasTodo[];

    return todos
      .map((todo) => {
        const assignment = todo.assignment;
        if (!assignment?.id || !assignment.name || !assignment.due_at) {
          return null;
        }

        const dueAt = assignment.due_at;

        return {
          id: String(assignment.id),
          title: assignment.name,
          course: todo.context_name || `Course ${assignment.course_id ?? ""}`.trim(),
          dueAt,
          estimatedMinutes: estimateMinutes(assignment.points_possible),
          priority: getPriorityFromDueDate(dueAt)
        };
      })
      .filter((item): item is Assignment => Boolean(item))
      .sort((a, b) => new Date(a.dueAt).getTime() - new Date(b.dueAt).getTime());
  } catch {
    return [];
  }
}
