import type { WeatherSnapshot } from "@/lib/types";

type OpenMeteoResponse = {
  current?: {
    temperature_2m?: number;
    weather_code?: number;
  };
};

function weatherCodeToCondition(code?: number) {
  if (code === undefined) return "Unknown";

  if (code === 0) return "Clear";
  if ([1, 2, 3].includes(code)) return "Partly Cloudy";
  if ([45, 48].includes(code)) return "Foggy";
  if ([51, 53, 55, 61, 63, 65, 80, 81, 82].includes(code)) return "Rain";
  if ([71, 73, 75, 77, 85, 86].includes(code)) return "Snow";
  if ([95, 96, 99].includes(code)) return "Thunderstorms";

  return "Cloudy";
}

export async function fetchWeather(lat: number, lon: number): Promise<WeatherSnapshot | null> {
  const url =
    `https://api.open-meteo.com/v1/forecast?latitude=${lat}&longitude=${lon}` +
    "&current=temperature_2m,weather_code&temperature_unit=fahrenheit";

  try {
    const res = await fetch(url, {
      method: "GET",
      headers: { Accept: "application/json" },
      next: { revalidate: 900 }
    });

    if (!res.ok) {
      return null;
    }

    const data = (await res.json()) as OpenMeteoResponse;
    const tempF = Math.round(data.current?.temperature_2m ?? 0);
    const condition = weatherCodeToCondition(data.current?.weather_code);

    return {
      condition,
      tempF,
      summary: `${condition} in San Luis Obispo. Plan focused study blocks around your class schedule.`
    };
  } catch {
    return null;
  }
}
