export const appConfig = {
  studentFirstName: process.env.NEXT_PUBLIC_STUDENT_FIRST_NAME || "Faith",
  studentInterests: (process.env.NEXT_PUBLIC_STUDENT_INTERESTS || "live music,coastal walks,coffee shops")
    .split(",")
    .map((value) => value.trim())
    .filter(Boolean),
  location: {
    lat: Number(process.env.NEXT_PUBLIC_SLO_LAT || 35.2828),
    lon: Number(process.env.NEXT_PUBLIC_SLO_LON || -120.6596)
  },
  canvas: {
    baseUrl: process.env.CANVAS_BASE_URL,
    token: process.env.CANVAS_API_TOKEN
  },
  foursquare: {
    apiKey: process.env.FOURSQUARE_API_KEY
  }
};

export function hasCanvasConfig() {
  return Boolean(appConfig.canvas.baseUrl && appConfig.canvas.token);
}

export function hasFoursquareConfig() {
  return Boolean(appConfig.foursquare.apiKey);
}
