import { getPlannerData } from "@/lib/planner-data";
import { getModeCopy, getUrgentAssignments, getWorkloadMode } from "@/lib/smart-mode";
import { CATEGORY_ORDER, type Assignment, type PlaceSuggestion } from "@/lib/types";

function formatDue(dateIso: string) {
  return new Date(dateIso).toLocaleString("en-US", {
    weekday: "short",
    hour: "numeric",
    minute: "2-digit"
  });
}

function AssignmentItem({ assignment }: { assignment: Assignment }) {
  return (
    <li className="tile task-item">
      <p className="eyebrow">{assignment.course}</p>
      <h4>{assignment.title}</h4>
      <p>Due {formatDue(assignment.dueAt)}</p>
      <p>{assignment.estimatedMinutes} min estimate</p>
    </li>
  );
}

function PlaceItem({ place }: { place: PlaceSuggestion }) {
  return (
    <li className="tile place-item">
      <h4>{place.name}</h4>
      <p>{place.info}</p>
      <p>{place.distanceMiles} mi away</p>
      <span className={place.openNow ? "status open" : "status closed"}>
        {place.openNow ? "Open now" : "Check hours"}
      </span>
    </li>
  );
}

export const dynamic = "force-dynamic";

export default async function HomePage() {
  const data = await getPlannerData();
  const urgent = getUrgentAssignments(data.assignments);
  const mode = getWorkloadMode(data.assignments);
  const modeCopy = getModeCopy(mode, urgent.length);

  const suggestionsByCategory = CATEGORY_ORDER.map((category) => ({
    category,
    items: data.suggestions.filter((item) => item.category === category)
  }));

  return (
    <main className="shell">
      <section className="hero reveal">
        <p className="eyebrow">SLO Student Day Planner</p>
        <h1>
          Hello, {data.profile.firstName} <span aria-hidden>ðŸ‘‹</span>
        </h1>
        <p>
          {data.weather.condition}, {data.weather.tempF}F. {data.weather.summary}
        </p>
      </section>

      <section className="grid-two reveal delay-1">
        <article className="panel">
          <h2>Today&apos;s Top 3 Priorities</h2>
          <ul className="list">
            {data.assignments.slice(0, 3).map((assignment) => (
              <AssignmentItem key={assignment.id} assignment={assignment} />
            ))}
          </ul>
        </article>

        <article className="panel mode-panel">
          <p className="eyebrow">Smart Mode</p>
          <h2>{modeCopy.title}</h2>
          <p>{modeCopy.body}</p>
          <div className="mode-tag">Mode: {mode.replace("_", " ")}</div>
        </article>
      </section>

      <section className="panel reveal delay-2">
        <h2>What&apos;s Open Around You (SLO)</h2>
        <div className="categories">
          {suggestionsByCategory.map(({ category, items }) => (
            <article key={category} className="category-block">
              <h3>{category}</h3>
              <ul className="list compact">
                {items.map((place) => (
                  <PlaceItem key={place.id} place={place} />
                ))}
              </ul>
            </article>
          ))}
        </div>
      </section>

      <section className="grid-two reveal delay-3">
        <article className="panel">
          <h2>Suggested After-Class Plan</h2>
          <ul className="list">
            <li className="tile">Grab supplies at University Store before your STAT set.</li>
            <li className="tile">Book a quick maker session for your CSC milestone.</li>
            <li className="tile">Plan dinner at Firestone Grill before evening study.</li>
          </ul>
        </article>

        <article className="panel">
          <h2>Data Sources to Plug In</h2>
          <ul className="list">
            <li className="tile">Canvas API: classes, grades, assignment deadlines</li>
            <li className="tile">Weather API: current + hourly forecast</li>
            <li className="tile">Maps/Places API: nearby open locations by category</li>
            <li className="tile">User profile: interests, preferred activity types</li>
          </ul>
        </article>
      </section>
    </main>
  );
}
