import { appConfig } from "@/lib/config";
import type { CategoryKey, PlaceSuggestion } from "@/lib/types";

type FoursquareSearchResponse = {
  results?: Array<{
    fsq_id?: string;
    name?: string;
    distance?: number;
    categories?: Array<{ name?: string }>;
    hours?: {
      open_now?: boolean;
    };
  }>;
};

const queryByCategory: Record<CategoryKey, string> = {
  Restaurants: "restaurant",
  Coffee: "coffee shop",
  Movies: "movie theater",
  Shopping: "shopping center",
  Beaches: "beach",
  Hikes: "hiking trail",
  "Game Stores": "game store",
  Makerspaces: "makerspace",
  "School Supplies": "office supply store",
  "Music Events": "live music venue",
  "Farmers Market": "farmers market",
  "Sporting Events": "stadium",
  Performances: "performing arts theater"
};

function toMiles(distanceMeters?: number) {
  if (!distanceMeters) return 0;
  return Math.round((distanceMeters * 0.000621371) * 10) / 10;
}

async function fetchOneCategory(category: CategoryKey, apiKey: string): Promise<PlaceSuggestion | null> {
  const query = queryByCategory[category];
  const ll = `${appConfig.location.lat},${appConfig.location.lon}`;
  const url =
    `https://api.foursquare.com/v3/places/search?query=${encodeURIComponent(query)}` +
    `&ll=${encodeURIComponent(ll)}&radius=10000&limit=1&sort=DISTANCE`;

  const res = await fetch(url, {
    headers: {
      Accept: "application/json",
      Authorization: apiKey
    },
    next: { revalidate: 1800 }
  });

  if (!res.ok) {
    return null;
  }

  const data = (await res.json()) as FoursquareSearchResponse;
  const item = data.results?.[0];

  if (!item?.name) {
    return null;
  }

  const descriptor = item.categories?.[0]?.name || query;

  return {
    id: item.fsq_id || `${category.toLowerCase().replace(/\s+/g, "-")}-fallback`,
    category,
    name: item.name,
    distanceMiles: toMiles(item.distance),
    info: `${descriptor} near campus`,
    openNow: Boolean(item.hours?.open_now)
  };
}

export async function fetchPlacesByCategory(categories: CategoryKey[], apiKey: string): Promise<PlaceSuggestion[]> {
  const settled = await Promise.allSettled(
    categories.map((category) => fetchOneCategory(category, apiKey))
  );

  return settled
    .map((result) => (result.status === "fulfilled" ? result.value : null))
    .filter((item): item is PlaceSuggestion => Boolean(item));
}
