import { appConfig, hasCanvasConfig, hasFoursquareConfig } from "@/lib/config";
import { plannerData as fallbackData } from "@/lib/mock-data";
import { fetchCanvasAssignments } from "@/lib/providers/canvas";
import { fetchPlacesByCategory } from "@/lib/providers/places";
import { fetchWeather } from "@/lib/providers/weather";
import { CATEGORY_ORDER, type CategoryKey, type PlannerData, type PlaceSuggestion } from "@/lib/types";

function cloneFallback(): PlannerData {
  return {
    profile: {
      firstName: fallbackData.profile.firstName,
      interests: [...fallbackData.profile.interests]
    },
    weather: { ...fallbackData.weather },
    assignments: [...fallbackData.assignments],
    suggestions: [...fallbackData.suggestions]
  };
}

function mergeSuggestions(
  liveSuggestions: PlaceSuggestion[],
  fallbackSuggestions: PlaceSuggestion[]
): PlaceSuggestion[] {
  const bestByCategory = new Map<CategoryKey, PlaceSuggestion>();

  for (const suggestion of liveSuggestions) {
    bestByCategory.set(suggestion.category, suggestion);
  }

  for (const suggestion of fallbackSuggestions) {
    if (!bestByCategory.has(suggestion.category)) {
      bestByCategory.set(suggestion.category, suggestion);
    }
  }

  return CATEGORY_ORDER.map((category) => bestByCategory.get(category)).filter(
    (item): item is PlaceSuggestion => Boolean(item)
  );
}

export async function getPlannerData(): Promise<PlannerData> {
  const base = cloneFallback();

  base.profile.firstName = appConfig.studentFirstName;
  base.profile.interests = appConfig.studentInterests;

  const [weatherResult, assignmentsResult, placesResult] = await Promise.all([
    fetchWeather(appConfig.location.lat, appConfig.location.lon),
    hasCanvasConfig()
      ? fetchCanvasAssignments(appConfig.canvas.baseUrl!, appConfig.canvas.token!)
      : Promise.resolve([]),
    hasFoursquareConfig()
      ? fetchPlacesByCategory(CATEGORY_ORDER, appConfig.foursquare.apiKey!)
      : Promise.resolve([])
  ]);

  if (weatherResult) {
    base.weather = weatherResult;
  }

  if (assignmentsResult.length > 0) {
    base.assignments = assignmentsResult;
  }

  if (placesResult.length > 0) {
    base.suggestions = mergeSuggestions(placesResult, base.suggestions);
  }

  return base;
}
